import React, { Component } from "react"
import Helmet from 'react-helmet';

import './style.css';

import { Link } from 'react-router-dom';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import { faTools } from '@fortawesome/free-solid-svg-icons'
import { faBalanceScaleLeft } from '@fortawesome/free-solid-svg-icons'
import { faHandSpock } from '@fortawesome/free-solid-svg-icons'
import { faFileAlt } from '@fortawesome/free-solid-svg-icons'

class LandingPage extends Component{
    renderHelmet() {
        // const translatedTitle = formatMessage(this.props.intl, messages, this.props.route.name);
        return (
          <Helmet
            title= "GreenGolden"
            meta={[{ name: 'description', content: 'LendingCar' }]}
          />
        );
      }

      renderHeadliner(handle) {
        return (
          <div className="background-image" id="search">
            <h1 className="sub-title font-regular">Golden Green Investment</h1>
    
            <div className = "option-container">
    
              <div className ="option-containt">
                <FontAwesomeIcon icon={faBalanceScaleLeft} className="option-awefont"/>
                {/* <Link className="option-a" to="#properties">Properties</Link> */}
                <a  className="option-a">Properties</a>
              </div>
    
              <div className ="option-containt">
                <FontAwesomeIcon icon={faTools} className="option-awefont"/>
                {/* <Link className="option-a" to="#equipment">Equipment</Link> */}
                <a  className="option-a">Equipment</a>
              </div>
    
              <div className ="option-containt">
                <FontAwesomeIcon icon={faHandSpock} className="option-awefont" />
                <a  className="option-a">Sales</a>
              </div>
              <div className ="option-containt" >
                <FontAwesomeIcon icon={faFileAlt} className="option-awefont" />
                {/* <Link to="/fora"  className="option-a">Fora</Link> */}
                <a  className="option-a">Fora</a>
              </div>
            </div>
        </div>
        )}

    render(){
        return(
        <div>
            {this.renderHelmet()}
            {this.renderHeadliner()}
        </div>
        
        )
    }

}
export default LandingPage